.modal-dialog {
  top: 5%;
}

.createPassword_body {
  padding: 0 138px;
}

.createPassword_header {
  border: none;
  padding: 60px 0 10px 0;
}

.createPassword_header_text {
  font-size: 2rem;
  color: #888889;
  font-weight: 600;
  margin-bottom: 25px;
}
.close {
  position: absolute;
  top: 8px;
  right: 24px;
  border: none;
  color: #000000;
  background-color: transparent;
  font-size: 4rem;
}
.back_button {
  color: #000000;
  position: absolute;
  top: 10px;
  left: 24px;
  font-size: 3.5rem;
  border: none;
  background-color: transparent;
}

label {
  font-size: 1.6rem;
  color: #6b6b6c;
  font-weight: 600;
  margin-bottom: 12px;
}
.password_strength {
  font-size: 1.2rem;
  padding: 4px;
}
.weak {
  color: #ffba00;
}
.strong {
  color: #6cc04b;
}

.createPassword_inputField {
  height: 67px;
  font-size: 2rem;
  border: 1px solid #e2e2e2;
  margin-bottom: 10px;
}
.createPassword_inputField::placeholder {
  color: #4d4d4d60;
  font-weight: 500;
}
.createPassword_inputField.form-control:focus {
  border-color: rgb(147, 73, 222);
  box-shadow: 0 0 0 0.25rem rgb(147 73 222 / 25%);
}
.createPassword_inputField.invalid.form-control {
  border-color: rgb(255, 44, 44);
}
.password_input_field {
  display: flex;
  position: relative;
}
.password_eye_logo {
  position: absolute;
  height: 67px;
  right: 2%;
  font-size: 2.4rem;
  color: #888889;
  border: none;
  background-color: transparent;
}
.validation_checkmark {
  margin-top: 20px;
}
.validation_checkmark span {
  font-size: 1.6rem;
  padding-left: 10px;
  color: #2a2e43;
}
.characters_check {
  color: #ebe8e8;
  font-size: 2rem;
}

.uppercase_check {
  color: #ebe8e8;
  font-size: 2rem;
}
.number_check {
  color: #ebe8e8;
  font-size: 2rem;
}
.valid {
  color: #9349de;
}

.createPassword_checkbox {
  font-size: 2rem;
}

.createPassword_checkbox.form-check-input:checked {
  color: turquoise;
  background-color: #9349de;
  border-color: #9349de;
}
.createPassword_checkbox.form-check-input:focus {
  border-color: #9349de;
  /* outline: 0; */
  box-shadow: none;
}
.checkbox_form_group {
  display: flex;
  padding-top: 57px;
  /* align-items: center; */
}
.checkbox_form_group label {
  font-size: 1.4rem;
  padding-top: 6px;
  padding-left: 6px;
  /* padding: 9px 0; */
  /* align-self: flex-end; */
}

.createPassword_input_button {
  width: 345px;
  height: 64px;
  background-color: #6318af;
  font-size: 2rem;
  font-weight: bold;
  box-shadow: none;
  text-align: center;
  margin: 36px auto 50px auto;
  display: block;
}

.policy_link {
  color: #9349de;
}
@media (max-width: 991px) {
  .createPassword_body {
    padding: 0 40px;
  }
  .createPassword_input_button {
    width: 100%;
  }
}
